h1 {
  margin: 0px;
  font-family: 'Homemade Apple', cursive;
  font-size: 45px;
  color: #FFFDF9;
}

header {
  box-sizing: border-box;
  /* z-index: 98; */
  width: 100vw;
  height: 90px;
  padding: 5px 240px;
  background-color: black;
  position: sticky;
}

@media only screen and (max-width: 1100px) {
  header {
    padding: 5px 90px;
  }
}
